@import 'reset';
@import url('https://fonts.googleapis.com/css?family=Montserrat:regular,500,600,700');

body {
	background-color: #333;
	color: #fff;
	font-family: 'Montserrat';
}

.container {
	max-width: 1230px;
	margin: 0 auto;
	padding: 0 15px;
}

.wrapper {
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
}

.page {
	flex: 1 1 auto;
	padding-top: 20px;
	padding-bottom: 20px;
}

.content-catalog{
	display: flex;
	flex-wrap: wrap;
	margin: -10px;
}